<template>
  <div id="app">
    <TodoHeader></TodoHeader>
    <TodoInput @addTodoItem="addTodoItem"></TodoInput>
    <TodoList @completeTodo="completeTodo"  @removeTodo="removeTodo" :todos=todos></TodoList>
    <TodoFooter @clearTodoAll="clearTodoAll"></TodoFooter>
  </div>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue'
import TodoInput from './components/TodoInput.vue'
import TodoList from './components/TodoList.vue'
import TodoFooter from './components/TodoFooter.vue'
export default {
  data() {
    return {
      todos: []
    }
  },
  components : {
    TodoHeader,
    TodoInput,
    TodoList,
    TodoFooter
  },
  created(){
    for(var i = 0 ; i < localStorage.length ; i++){
      if(localStorage.key(i) != 'loglevel:webpack-dev-server'){
        this.todos.push(JSON.parse(localStorage.getItem(localStorage.key(i))))
      }
    }
  },
  methods: {
    addTodoItem(newTodoItem) {
      let item = {
          str : newTodoItem,
          done : false
      }
      localStorage.setItem(newTodoItem, JSON.stringify(item))
      this.todos.push(item)
    },
    clearTodoAll(){
      localStorage.clear()
      this.todos = []
    },
    removeTodo(str, index){
      this.todos.splice(index, 1)
      localStorage.removeItem(str)
    },
    completeTodo(modifyTodo, index){
      this.todos[index].done = !this.todos[index].done
      localStorage.removeItem(modifyTodo.str)
      localStorage.setItem(modifyTodo.str, JSON.stringify(this.todos[index]))
    }
  }
}
</script>

