<template>
    <div class="inputBox shadow">
      <input type="text" v-model="newTodoItem" @keypress.enter="addTodo" placeholder="내용을 입력해 주세요">
      <span class="addContainer" @click="addTodo">
          <i class="fas fa-plus addBtn"></i>
      </span>
    </div>
  </template>
  
  <script>
  export default {
      data() {
          return {
              newTodoItem : ""
          }
      },
      methods: {
          addTodo(){
              if(this.newTodoItem == ""){
                  alert('입력된 내용이 없습니다.')
              }else{
                  this.$emit('addTodoItem', this.newTodoItem)
                  this.newTodoItem = "" 
              }
          }
      }
  }
  </script>
  
  <style scoped>
  input:focus {
    outline: none;
  }
  .inputBox {
    background: white;
    height: 50px;
    line-height: 50px;
    border-radius: 5px;
  }
  .inputBox input {
    border-style: none;
    font-size: 0.9rem;
    width: 80%;
  }
  .addContainer {
    float: right;
    background: linear-gradient(to right, #6478FB, #8763FB);
    display: block;
    width: 3rem;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
  }
  .addBtn {
    cursor: pointer;
    color: white;
    vertical-align: middle;
  }
  .closeModalBtn {
    color: #42b983;
  }
  </style>