<template>
  <ul>
    <li v-for="(todo, index) in todos" :key=todo.str>
        <i class="checkBtn fas fa-check" :class="{checkBtnCompleted: todo.done}"
            @click="toggleComplete({todo, index})"></i>
        <span :class="{textCompleted: todo.done}">{{todo.str}} </span>
        <span class="removeBtn" v-on:click="removeTodo({todo, index})">
          <i class="fas fa-trash-alt"></i>
        </span>
    </li>
  </ul>
</template>

<script>
export default {
    props: ["todos"],
    methods: {
      toggleComplete(modifyTodo) {
        this.$emit('completeTodo', modifyTodo.todo, modifyTodo.index)
      },
      removeTodo(modifyTodo) {
        this.$emit('removeTodo', modifyTodo.todo, modifyTodo.index)
      }
    }

}
</script>

<style scoped>
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: left;
}
li {
  display: flex;
  min-height: 50;
  height: 50px;
  line-height : 50px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background: white;
  border-radius: 5px;
}
.checkBtn {
  line-height: 50px;
  color: #62acde;
  margin-right: 5px;
}
.removeBtn {
  margin-left: auto;
  color : #de4343;
}
.checkBtnCompleted {
color : #b3adad;
}
.textCompleted {
  text-decoration: line-through;
  color: #b3adad;
}
/* 리스트 아이템 트랜지션 효과 */
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
</style>